body {
    background: #000;
}
#loading.loading__active {
    transition: all 1.6s;
    opacity: 1;
}
#loading {
    opacity: 0;
    transition: all 1.6s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    background: var(--black);
}
.loading__text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100px;
    line-height: 100px;

    span {
        display: inline-block;
        margin: 0 5px;
        color: var(--white);
    }
    @for $i from 0 through 7 {
        span:nth-child(#{$i+1}) {
        filter: blur(0px);
        animation: blur-text 1.5s $i*0.2s infinite linear alternate;
      }
    }
