    // }
}
#loading.light {
    background-color:var(--light_bg);
}
.loading__text.light span {
    color: var(--black);
}
@keyframes blur-text {
    0% {
        filter: blur(0px);
    }
    100% {
        filter: blur(4px);
    }
}